.order_tarcking_line {
  width: 78%;
  top: 42%;
  left: 40px;
  z-index: 1;
  position: absolute;
  border-top: 2px solid rgb(44, 41, 41);
}
.order_tarcking {
  z-index: 11;
}
.order_round {
  width: 20px;
  height: 20px;
  background: rgb(175, 175, 105);
  border-radius: 50%;
  line-height: 2px;
  margin-top: 10px;
  margin-bottom: 2px;
}
.center {
  text-align: center;
}
.print_btn {
  float: right;
}
.activeTrack {
  background: green;
}
.dotedBorderButton {
  border: none;
  border-top: 3px dashed #f00;
  color: #fff;
  background-color: #fff;
  height: 4px;
  margin-bottom: 30px;
}
.invoice {
  position: absolute;
  z-index: -1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-size: 25px;
  font-weight: bold;
  letter-spacing: 2px;
  width: 100%;
  height: 100%;
}
